.fancytree-list ul.fancytree-container{
  border: 1px solid #ccc;
border-radius: 4px;
-webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
}

span.fancytree-node.fancytree-active {
  background-color: #ddd;
}

span.fancytree-node.fancytree-selected {
background-color: #ddd;
border-color: #ddd;
}
span.fancytree-node.fancytree-selected span.fancytree-title {
background-color: #ddd;
}

span.fancytree-node.fancytree-active {
  background-color: #ddd;
}

.fancytree-container.fancytree-treefocus span.fancytree-node:hover {
  background-color: #eee;
}
.fancytree-container.fancytree-treefocus span.fancytree-node.fancytree-focused {
  border-color: #ddd;
}
.fancytree-container.fancytree-treefocus span.fancytree-node.fancytree-selected {
  background-color: #ddd;
}
.fancytree-container.fancytree-treefocus span.fancytree-node.fancytree-selected span.fancytree-title {
  background-color: #ddd;
}
.fancytree-container.fancytree-treefocus span.fancytree-node.fancytree-active {
  background-color: #ddd;
  border-color: #ddd;
}

ul.fancytree-ext-wide span.fancytree-node span.fancytree-title {
  position: relative;
  z-index: 0;
  width: 100%;
  padding-left: 503px;
  margin-left: -500px;
}

ul.fancytree-ext-wide span.fancytree-node>span {
  position: relative;
  z-index: 0;
}

.fancytree-container {
/*  display: inline-block;*/
  /*width: 200px;*/
  max-height: 200px;
  overflow: auto;
}

ul.fancytree-container {
  font-family: tahoma,arial,helvetica;
  font-size: 10pt;
  white-space: nowrap;
  padding: 7px;
  margin: 0;
  background-color: #fff;
  border: 1px dotted gray;
  overflow: auto;
  min-height: 0;
  position: relative;
}

span.fancytree-node span.fancytree-expander:hover {
    color: #040404;
}