div.dataTables_wrapper {
    padding-bottom: 30px;
    width: 100%;
    margin: 0 auto;

th, td { 
    white-space: nowrap;
 }
    a.btn {
        //margin-bottom: 5px;
        //line-height: 0.5;
    }
}



table.dataTable thead th, table.dataTable thead td {
  padding: 10px 18px;
  border-bottom: 1px solid #C3C2C2;
}

/* table.dataTable.display tbody td {
  border-top: 1px solid #C3C2C2;
} */
.scrollX {
  overflow: auto; width: 100%;
}

 .dataTables_wrapper.no-footer .dataTables_scrollBody {
border-bottom: 1px solid #C3C2C2;
} 

table.dataTable.no-footer {
  border-bottom: 1px solid #C3C2C2;
}
.data-table-loader{
  position:absolute;
  margin-top:70px;
  font-size:150%;
}

tbody tr.odd, table.dataTable.display tbody tr.odd {
	// background-color: #F0F0F0;
    background-color: #FFF;
}

table.dataTable.display tbody tr.even>.sorting_1, table.dataTable.order-column.stripe tbody tr.even>.sorting_1 {
	background-color: #BAB7B7;
}

.table-striped>tbody>tr:nth-of-type(odd), table.dataTable>tbody>tr:nth-of-type(odd) {
  background-color: #F0F0F0;
}

table.dataTable > tbody > tr.child {

    span.dtr-title {
        min-width: 120px;
    }
   
    span.dtr-data, span.dtr-title {
        
        white-space: normal;

        @media (max-width: 560px) {
            width: 100%;
            min-width: 20px;
        }
    } 
}

table.dataTable > {
    tbody, tfoot, tfoot, thead  {
        >tr>td/* , >tr>th */ {
            white-space: normal;
        }
    }
}
// Responsive datatable expand/collabs button
table.dataTable.dtr-inline.collapsed>tbody>tr>td:first-child:before, 
table.dataTable.dtr-inline.collapsed>tbody>tr>th:first-child:before {
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    display: block;
    position: absolute;
    color: #fff;
    border: none;
    border-radius: 0;
    box-shadow: none;
    box-sizing: content-box;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    line-height: 14px;
    content: '+';
    background-color: #337ab7;
    padding-top: 5px;
    opacity: 0.8;
    border-bottom-right-radius: 4px;
}

table.dt-rowReorder-float{position:absolute !important;opacity:0.8;table-layout:fixed;outline:2px solid #888888;outline-offset:-2px;z-index:2001}tr.dt-rowReorder-moving{outline:2px solid #555555;outline-offset:-2px}body.dt-rowReorder-noOverflow{overflow-x:hidden}table.dataTable td.reorder{text-align:center;cursor:move}

table.dataTable.dtr-inline.collapsed > tbody > tr[role="row"] > td:first-child:before, table.dataTable.dtr-inline.collapsed > tbody > tr[role="row"] > th:first-child:before {
    border-radius: 25px;
    line-height: 10px;
    height: 14px;
    width: 19px;
}