/*
 * Base structure
 */

/* Move down content because we have a fixed navbar that is 50px tall */
body {
  padding: 0;
  overflow-x: initial;
  padding-top: 70px;
  padding-top: 80px;
   /* Margin bottom by footer height */
  // margin-bottom: 60px;
    font-size: 0.98rem;
    padding-bottom: 50px;

  a, .page-link {
    color: $primaryHighlightColor;
    outline: 0;
  }

    .btn-primary {
        background-color: $primaryHighlightColor;
        border-color: $primaryHighlightColor;
    }

    .btn-outline-*{
        background-color: #fff;
    }

    .btn-outline-primary {
        color: $primaryHighlightColor;
        background-color: #fff;
        // background-color: transparent;
        background-image: none;
        border-color: $primaryHighlightColor;
    }

    .btn-outline-primary:hover {
        color: #fff;
        background-color: $primaryHighlightColor;;
        border-color: $primaryHighlightColor;;
    }

  .page-item.active .page-link {
    background-color: $primaryHighlightColor;
    border-color: $primaryHighlightColor;
  }
}

html {
    overflow-y:scroll;
    position: relative;
    min-height: 100%;

    .lw-disabling-block .lw-processing-window {
        display:none;
    }
}

@mixin lwTransition($time:0.3s) {
    -webkit-transition  : all $time ease-in-out;
    -moz-transition     : all $time ease-in-out;
    -ms-transition      : all $time ease-in-out;
    -o-transition       : all $time ease-in-out;
    transition          : all $time ease-in-out;
}

html.lw-has-disabled-block {
    
    pointer-events: none;
    cursor: wait;
    -webkit-user-select: none;  
    -moz-user-select: none;    
    -ms-user-select: none;  
     overflow:hidden;
     // margin-right:14px;

     body {
        overflow-y:scroll;
     }

    &.ngdialog-open body {
        overflow-y:hidden;
    }

    .lw-disabled-block {
        pointer-events: none;
        cursor: wait;
        // opacity: 0.75;
        -webkit-user-select: none;  
        -moz-user-select: none;    
        -ms-user-select: none;      
        user-select: none;
        overflow:hidden;
        display: block;
        // background-color:#ffffff; 
        background:transparent;
        height: 100%;
        width: 100%;
        position: fixed;
        z-index: 10002;

        &.lw-has-processing-window {
            background:rgba(0, 0, 0, 0.2);

            .lw-processing-window { 
                display:block;
            }
        }
    }

    .lw-processing-window {
            display:none;
            height:24%;
            min-height:150px;
            width:40%;
            background:rgba(255, 255, 255, 0.85);
            position:absolute;
            top:38%;
            left:30%;
            border:10px solid rgba(255, 255, 255, 0.7);
            border-radius:8px;
            text-align:center;

            .loader {
                margin: 2% auto 70px auto;
            }

            @media (max-width: 776px) {
                width:90%;
                left:2.5%;
                height:auto;
            }
        }

    .ngdialog-content {
        pointer-events: none;
    }
}

