/*
 Bootstrap Material DateTimePicker
-------------------------------------------------------------------------- */
.dtp {
    z-index: 10002;
}

// Bootstrap Material DateTimePicker
.dtp {
    overflow: auto;
    height: auto;   
    
    a {
        border:none;
    }

    circle.dtp-select-hour:not([fill=transparent]), circle.dtp-select-minute:not([fill=transparent]) {
         fill: #2852BF;
    }

    line.hour-hand, line.minute-hand {
        stroke: #2852BF;
    }
    
    &> .dtp-content {
        max-height: 600px;
        padding-bottom: 15px;

        .dtp-date-view {
            width: 100%;
            
            & > header.dtp-header {
                background: #002A96;
                color: #fff;
            }

        }
    }

    .dtp-buttons {
        padding: 0 1rem 1rem 1rem;
        text-align: right;

        .lw-btn {
            margin-right:8px;
        }
    }

    div.dtp-picker {

    }

    .p10 > a {
        color: #C7C7C7;
    }

    .dtp-picker-time > a.dtp-select-hour.selected,
    .dtp-actual-meridien a.selected,
    .dtp-hand.on,
    .dtp-picker-time > a.dtp-select-minute.selected,
    div.dtp-date, div.dtp-time,
    table.dtp-picker-days tr > td > a.selected {
        background: #2852BF;
        color: #fff;
    }

    table {
        width: 100%;
        max-width: 100%;
    }

   /*  div.dtp-picker-clock {
        padding: 17px 14px 3px 24px;
        margin: 10px 12px 5px 30px;
    } */

    div.dtp-picker-clock {
    }


    table.dtp-picker-days {

        tr > td {
            text-align: center;
            font-weight: normal;
            font-size: inherit;
            padding: 0;
        }
    }
}

